*{
    margin:0;
    padding:0;
    /* box-sizing: border-box; */
}
html{
    scroll-behavior: smooth;
}
body{
    background-color: #80808014;
}
.logo
{
    display:flex;
    justify-content: center;
    align-items: center;
    width:20%;

}
.logo img{
    width:50%;

}
.navbar{
    display:flex;
    justify-content: center;
    align-items: center;
    cursor:pointer;
    position:sticky;
    top:0;
  
}
/* ul */
.ul{
  width:82%;
  display:flex;
  align-items: center;
}
.ul li{
list-style: none;
padding:16px 30px;

}
.ul li a{
text-decoration: none;
color:white;
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
font-size:20px;
}

.ul li a:hover{
color:grey;

}


/* right */
.right{
    width:50%;
    text-align: right;
    padding:0px 35px;
  
}
#search{
    padding: 3px 5px;
    font-size: 15px;
    border: 2px solid black;
    border-radius: 20px;
    outline:none;
}

.searchbtn{
    padding:2px 14px;
    border: 3px solid rgb(255, 255, 255);
    border-radius: 50px;
    background: none;
    color:white;
    cursor:pointer;
}

.background{
    background:rgba(0,0,0,0.7) url('pics/b2.jpg');
    background-size: cover; 
     background-blend-mode:darken;
}
.firstsection{
    height:70vh;

    
}
.mainbox{
    height:80%;
    display:flex;
    justify-content: center;
    align-items: center;
    color:white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    width:80%;
    margin:auto;
   
    
}
.shalf{
   
    display:flex;
    justify-content: center;
    align-items: center;

    
}
.shalf img{
    width:500px;
}

.fhalf{
    display:flex;
    flex-direction: column;
    justify-content: center;
   
    
}
.bigtext{
font-size:32px;
font-weight:bold;
}
.smalltext{
    font-size:16px;
}

.btn{
    
    padding: 6px 20px;
    margin: 12px 0px;
    font-size: 18px;
    border: 3px solid rgb(255, 255, 255);
    border-radius: 50px;
    background: none;
    color:white;
    cursor:pointer;
}
.button{
    margin-top:5px;
}

.photo img{
    width:95%;
}
.photori img{
    width: 145%;
    margin-right: 113px;
    margin-top: 14px;
}
.photo {
   display:flex;
   justify-content: center;
   align-items: center;
   width:100%;
}
.photori{
   display:flex;
   justify-content: center;
   align-items: center;
   width:100%;
}
.section{
    display:flex;
    align-items: center;
    justify-content: space-evenly;
    max-width:90%;
    margin:auto;
    padding:18px 0px;
    
   
}
.left{

    flex-direction: row-reverse;
  margin:auto;

}
.left .photo img{
    width: 193%;
    margin-left: 120px;
  
}
.para{
    /* padding: 0px 55px; */
    margin-right: 125px;
  

}
.left .para{
    padding: 0px 25px;
    margin-left: 125px;
}
  


.sectionsubtag{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.sectiontag{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    padding:6px 0px;
}

.contact{
    background-color: grey;
    height:600px;
    margin-top:10px;
 
}
.form{
    width: 600px;
 
    margin:auto;
}
.form .design{
    margin-top: 18px;
    padding: 2px 2px;
    width: 100%;
    font-size: 18px;
    border: 2px solid rgb(0, 0, 0);
    border-radius: 6px;
    outline: none; 
    
      font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.contact h2{
    padding-top: 20px;
    font-size: 25px;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color:rgb(0, 0, 0); 
      text-align: center;
      font-weight: bold;
}

.dark{


    display:block;
    margin:auto;
    margin-top:20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    border:3px solid white;
    background-color:#0000006b;
    /* font-weight: bold;; */
}

.footer{
    padding:8px 2px;
    text-align: center;
    color:white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

}
.burger{
    display:none;
    position: absolute;
    right:30px;
    top:18px;
    
}

.line{
    background-color: white;
    height:4px;
    width:42px;
    margin:6px 3px;
}
